export default ".header {\n    position: fixed;\n    width: 100vw;\n    height: 75px;\n    background-color: #f7f7f7;\n    border-bottom: #dddddd 1px solid;\n    top: 0;\n}\n\n.nav-bar-element-container {\n    padding-right: 24px;\n}\n\n.nav-bar-element {\n    float: right;\n    list-style-type: none;\n    text-align: right;\n    padding-left: 15px;\n    line-height: 75px;\n}\n\n.nav-bar-element a {\n    text-decoration: none; /* no underline */\n    font-size: 24px;\n    position: relative;\n}\n\n.nav-bar-element a:before {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 2px;\n    bottom: 0;\n    left: 0;\n    background-color: #000;\n    visibility: hidden;\n    transform: scaleX(0);\n    transition: all 0.3s ease-in-out 0s;\n}\n\n.nav-bar-element a:hover:before {\n    visibility: visible;\n    transform: scaleX(1);\n}\n\n.hamburger {\n    position: fixed;\n    right: 24px;\n    top: 27.5px;\n    overflow: visible;\n}\n\nspan {\n    display: block;\n    width: 30px;\n    height: 4px;\n    margin-bottom: 4px;\n    position: relative;\n    background: black;\n    border-radius: 3px;\n    transition: all 0.2s cubic-bezier(.25, .75, .5, 1.25), opacity 0.2s cubic-bezier(1,-2,1,1) ;\n}\n\n.checked span:nth-child(1) {\n    transform: translate(0, 8px);\n}\n\n.crossed span:nth-child(2) {\n    transform: rotate(45deg);\n}\n\n.crossed span:nth-child(3) {\n    transform: rotate(-45deg);\n}\n\n.crossed span:nth-child(1) {\n    transition: opacity 0s;\n    opacity: 0;\n}\n\nspan:nth-child(2) {\n    position: fixed;\n}\n\n.checked span:nth-child(4) {\n    transform: translate(0, -8px);\n}\n\n.crossed span:last-child {\n    transition: opacity 0s;\n    opacity: 0;\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"